.swipe__buttons {
    position: fixed;
    bottom: 10vh;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.swipe__buttons .MuiIconButton-root{
    background-color: white;
    box-shadow: 0 10px 53px 0 rgba(0, 0, 0, 0.3) !important;
}

.swipeButtons__repeat{
    padding:3vm !important;
    color: #f5b748 !important;
}

.swipeButtons__left{
    padding:3vm !important;
    color: #ec5e6f !important;
}

.swipeButtons__star{
    padding:3vm !important;
    color: #62b4f9 !important;
}

.swipeButtons__right{
    padding:3vm !important;
    color: #76e2b3 !important;
}

.swipeButtons__lightning{
    padding:3vm !important;
    color: #915dd1 !important;
}
